body {
    padding-top: 56px; }

.flat {
    background: none;
    border: 0 none; }

.clear:after {
    content: '';
    display: block;
    clear: both; }

.fl {
    float: left; }
.fr {
    float: right; }

.both-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY( -50% ); }

#cards {
    margin: 0;
    list-style: none; }

// card-sizing
.grid-size {
    width: 33.3333%; }

.cd-4 {
    width: 100%; }

.cd-3 {
    width: 75%; }

.cd-2 {
    width: 50%; }

.cd-1 {
    width: 25%;
    header {
        height: 34px;
        line-height: 34px; } }

.cd {
    box-sizing: border-box;
    width: 33.3333%;
    display: inline-block;
    padding: 10px; }

.card-wrap {
    border-radius: 10px;
    overflow: hidden;
    position: relative; }

.cd-content {
    position: relative;
    padding: 1.5em;
    color: white; }

.quote {
    font-size: 2em;
    font-weight: bold; }

.quotes {
    margin: 0;
    opacity: .9; }

.quotes, .quote {
    &:before, &:after {
        content: '"'; } }

.desc {
    font-weight: bold; }

.desc2 {
    opacity: .9; }

.cd-footer {
    position: relative;
    overflow: hidden;
    padding: 1em;
    color: white;

    p {
        margin: 0; }

    .desc2 {
        opacity: .9; } }


.string-link {
    color: white;
    font-size: 1.1em;

    &:after {
        content: '>';
        margin-left: 6px; }

    &.single {
        padding: 0;
        padding-top: 1em; } }

.tr {
    text-align: right; }

.dis {
    color: white;
    opacity: .8; }

.cd-ui-container {
    padding: 0 1.5em 1em 1.5em;
    padding-top: 0;
    color: white; }
